import Home from '~/containers/Home/container'
import About from '~/containers/About/container'
export default [
  {
    path: '/',
    exact: true,
    component: Home,
  },

  {
    path: '/about',
    component: About,
  },
  {
    path: '/about/:id',
    component: About,
  },
];



